import Vue from "vue";
//引入vue-router
import VueRouter from "vue-router";

Vue.use(VueRouter);
import Check from "../components/Check";
import Upload from "../components/Upload";
export default new VueRouter({
  routes: [
    {
      path: "/",
      component: Check,
    },
    {
      path: "/upload",
      component: Upload,
    },
    {
      path: "/check",
      component: Check,
    },
  ],
});
