<template>
  <div>
    <h2>目录</h2>

    <h3 v-if="menu.length == 0">没有文件</h3>
    <table class="table" id="table1" v-else>
      <tr>
        <th>序号</th>
        <th>文件</th>
        <th>操作</th>
        <th>删除</th>
      </tr>
      <tr v-for="(item, index) in menu" :key="index">
        <td>{{ index + 1 }}</td>
        <td>{{ item }}</td>
        <td><a :href="'/public/files/' + item">下载</a></td>
        <td>
          <button
            class="btn btn-outline-danger"
            @click="deletefile(index)"
            type="button"
          >
            删除文件
          </button>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "Check",
  data() {
    return {
      menu: [],
    };
  },
  methods: {
    getInfo() {
      let _that = this;
      $.get("/menu", (res) => {
        _that.menu = [];
        res.forEach((item) => {
          _that.menu.unshift(item);
        });
      });
    },
    deletefile(i) {
      let _that = this;
      console.log(i);
      let result = confirm("确定删除" + _that.menu[i]);
      if (result === true) {
        $.get("/delete?file=" + _that.menu[i], (res) => {
          alert(res);
          _that.getInfo();
        });
      }
    },
  },
  mounted() {
    this.getInfo();
  },
};
</script>

<style scoped>
h3,
h2 {
  text-align: center;
}

.btn {
  margin: 10px;
}
a {
  text-decoration: none;
}

table {
  text-align: center;
}
</style>