<template>
  <div id="upload">
    <form
      method="post"
      id="file"
      action="/upload"
      onsubmit="return false"
      enctype="multipart/form-data"
    >
      <div class="mb-3">
        <input class="form-control" type="file" id="reportXML" name="file" />
      </div>
      <div id="status">0%</div>
      <button class="btn btn-primary" type="submit" @click.prevent="upload">
        上传
      </button>
    </form>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "Upload",
  methods: {
    upload() {
      if ($("#file input").val() === "") {
        alert("未选择任何文件!");
      }
      let options = {
        clearForm: true,
        url: "/upload",
        uploadProgress: (event, position, total, percentComplete) => {
          $("#status").html(percentComplete + "%");
        },
        success: function (message) {
          alert(message);
          $("#status").html("0%");
        },
      };
      $("#file").ajaxSubmit(options);
      return false;
    },
  },
};
</script>
<style scoped>
#upload {
  margin-top: 50vh;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-content: center;
}
#file {
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}
</style>